//   background-size: contain;
//   background-position: center;
//   background-repeat: no-repeat;
//   background-size: auto, 346px 346px;

  @media screen and (min-width: 320px) {
     background-position: 70% 0, 80% 0;
    background-size: auto;
    background-position: 70% 100%, 100% 55%;
  }

  @media screen and (min-width: 480px) {
    width: 480px;
     background-position: 45% 100%, 50% 60%;
     }
  @media screen and (min-width: 768px) {
    width: 768px;
      background-image: url(../images/tab/arrow.svg);
    background-size: 18px, auto;
    background-position: 154px 347px;
    background-repeat: no-repeat;
  }
  @media screen and (min-width: 1280px) {
    width: 1280px;
    height: 665px;
    padding-left: 0;
        background-image: url(../images/tab/arrow.svg);
    background-size: 26px , auto;
    background-position: 295px 583px;
    background-repeat: no-repeat;
  }
}
.hero__icecream-container-320 {
      background-image: url(../images/tel/hero-tel-1x.png), url(../images/tel/Ellipse-tel-1x.png);
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(../images/tel/hero-tel-2x.png), url(../images/tel/Ellipse-tel1-2x.png);
  }
 

  @media screen and (min-width: 320px) {
      height:  502px;
       background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto, 346px 346px;
    // padding-left: 20px;
    // padding-top: 101px;
    background-position: 70% 0, 80% 0;
    background-size: auto;
    background-position: 70% 100%, 100% 55%;
  }
  @media screen and (min-width: 480px) {
      display: none;
    
  }
} 
.hero__icecream-container-480 {
     @media screen and (min-width: 480px) {
    background-image: url(../images/tel/hero-tel-1x.png), url(../images/tel/Ellipse-tel1-1x.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/tel/hero-tel-2x.png), url(../images/tel/Ellipse-tel1-2x.png);
    }
    width: 480px;
    height: 502px;
    padding-left: 20px;
    // padding-right: 20px;
    background-position: 45% 100%, 50% 60%;
  background-repeat: no-repeat;
  }
@media screen and (min-width: 768px) {
display: none;
  }
}
.hero__icecream-container-768 {
  @media screen and (min-width: 768px) {
    // width: 768px;
    background-size: auto, auto;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url(../images/tab/hero-icecream-tab-1x.png),
      url(../images/tel/Ellipse-tel1-1x.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/tab/hero-icecream-tab-2x.png),
        url(../images/tel/Ellipse-tel1-2x.png);
    }
    // padding-top: 102px;
    height: 399px;
    // padding-left: 34px;
    background-position: 60% 100%, 67% 50%;

  }
  @media screen and (min-width: 1280px) {
    display: none;
    
  }
}
.hero__icecream-container-1280 {
      @media screen and (min-width: 1280px) {
    background-size: auto, 538px 538px;
    width: 1280px;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url(../images/desk/hero-icecream-desk-1x.png),
      url(../images/desk/Ellipse-desk-1x.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/desk/hero-icecream-desk-2x.png),
        url(../images/desk/Ellipse-des-2x.png);
    }
    height: 665px;
    padding-left: 0;
    background-position: 60% 100%, 67% 50%;
  }
}
.hero_title-container {
  max-width: 50%;
  @media screen and (min-width: 320px) {
    position: absolute;
    top: 193px;
    left: 20px;
  }
  @media screen and (min-width: 768px) {
    position: absolute;
    top: 102px;
    left: 34px;
   
  }
  @media screen and (min-width: 1280px) {
    position: absolute;
    top: 171px;
    left: 115px;

  }
}

.pretitle {
  @media screen and (min-width: 768px) {
    font-family: Titan One;
    font-size: 28px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.14;
    letter-spacing: 0em;
    color: $brand-color;
    position: relative;
    z-index: 1;
  }
  @media screen and (min-width: 1280px) {
    font-size: 46px;
    line-height: 1.15;
  }
}

.hero__cercle {
  position: relative;
  display: inline-block;
  border: #ffffff;
  width: 16px;
  height: 16px;
  background-color: #ffffff;
  border-radius: 50%;
  right: -8px;
  bottom: 35px;
  z-index: -1;
  @media screen and (min-width: 1280px) {
    width: 30px;
    height: 30px;
    right: -8px;
    bottom: 60px;
  }
}

.hero__figures {
  position: absolute;
  right: 34px;
  top: 96px;
  z-index: 1;
  @media screen and (max-width: 767px) {
    display: none;
  }
  @media screen and (min-width: 1280px) {
    right: 97px;
    top: 156px;
  }
}
.hero__figures-text {
  font-family: DM Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 8px;
  line-height: 10px;
  text-align: right;
  position: absolute;
  width: 43px;
  top: 30px;
  // right: -5px;
  color: #ffffff;
  @media screen and (min-width: 1280px) {
    right: 0px;
    top: 50px;
    width: 74px;
    font-size: 14px;
    line-height: 1.3;
  }
}

.hero__figures-16,
.hero__figures-23 {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  @media screen and (min-width: 1280px) {
    margin-bottom: 24px;
  }
}

.hero_girl-container {
  @media screen and (min-width: 768px) {
    background-image: url(../images/tab/hero-girl-tab-1x.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/tab/hero-girl-tab-2x.png);
    }
    background-repeat: no-repeat;
    width: 168px;
    height: 133px;
    position: absolute;
    z-index: 5;
    bottom: 0;
    right: 34px;
  }
  @media screen and (min-width: 1280px) {
    background-image: url(../images/desk/hero-girl-desk-1x.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/desk/hero-girl-desk-2x.png);
    }
    background-repeat: no-repeat;
    width: 293px;
    height: 232px;
    position: absolute;
    bottom: 0;
    right: 97px;
  }
}

.hero_milk-container {
  @media screen and (min-width: 768px) {
    background-image: url(../images/tab/hero-milk-tab-1x.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/tab/hero-milk-tab-2x.png);
    }
    background-repeat: no-repeat;
    width: 168px;
    height: 133px;
    position: absolute;
    bottom: 18px;
    left: 34px;
  }
  @media screen and (min-width: 1280px) {
    background-image: url(../images/desk/hero-milk-desk-1x.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/desk/hero-milk-desk-2x.png);
    }
    background-repeat: no-repeat;
    width: 188px;
    height: 191px;
    position: absolute;
    bottom: 48px;
    left: 77px;
  }
}
.hero__lorem-container{
     @media screen and (min-width: 768px) {
      position: absolute;
    top: 292px;
   left:  154px;
  }
    @media screen and (min-width: 1280px) {
      position: absolute;
    top: 485px;
   left:  295px;
  }
 
}

