.hero__container {
  @media screen and (max-width: 479px) {
  }
}

.hero__title {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 26px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #ffffff;
  margin-bottom: 19px;
  @media screen and (min-width: 480px) {
    font-size: 26px;
    line-height: 30px;
  }
  @media screen and (min-width: 768px) {
    //   padding-top: 10px;
    font-size: 22px;
    line-height: 1.14;
  }
  @media screen and (min-width: 1280px) {
    //   padding-top: 10px;
    font-size: 38px;
    line-height: 1.16;
    margin-bottom: 25px;
  }
}

.hero__passion {
  font-family: Titan One;
  font-size: 48px;
  font-style: normal;
  font-weight: 400;
  line-height: 46px;
  letter-spacing: 0.04em;
  text-align: left;
  color: $brand-color;
  @media screen and (min-width: 768px) {
    font-size: 34px;
    line-height: 0.9;
  }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.12;
  }
}

.button {
  padding: 8px 13px 8px 13px;
  border-radius: 20px;
  font-family: DM Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 8px;
  line-height: 1.25;
  border: none;
  margin-bottom: 66px;
  @media screen and (min-width: 1280px) {
    font-size: 14px;
    line-height: 1.3;
    padding: 11px 20px 11px 20px;
    margin-bottom: 112px;
  }
}

.hero__container-products-btn {
  background-color: $brand-color;
  color: #ffffff;
  margin-right: 8px;
  &:hover{
      background-color: #ECEBEB;
      border: 2px solid$brand-color;
      color: $brand-color;
  }
}

.hero__container-him-btn {
  background-color: #ffffff;
  color: $brand-color;
   &:hover{
      background-color: #ECEBEB;
      border: 2px solid$brand-color;
