.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  @include tab {
    padding-left: 34px;
    padding-right: 34px;
  }
}

.product-section-title {
  @include mobile__title--h3;
  display: block;
}

.product-subtitle {
  @include mobile__title--h2;
  margin-bottom: 144px;
  @include tab {
    margin-bottom: 150px;
  }
  @include desk {
    margin-bottom: 259px;
  }
}

.product__list {
  @include tab {
    display: flex;
    flex-wrap: wrap;
    margin-left: -20px;
    margin-top: -20px;
  }
  @include desk {
    margin-left: -30px;
    margin-top: -30px;
  }
  text-align: center;
}

.product__item {
  @include tab {
    flex-basis: calc(100% / 3 - 20px);
    margin-left: 20px;
    margin-top: 20px;
  }
  @include tab {
    min-width: 220px;
  }
  @include desk {
    max-width: 330px;
    height: 525px;
    margin-left: 30px;
    margin-top: 30px;
  }
  position: relative;
  display: inline-block;
  width: 280px;
  height: 485px;
  margin-bottom: 130px;
  text-align: center;
}
.product-card1 {
  background-color: $subtitle-color;
  border-radius: 24px;
}

.product-card2 {
  background-color: #f0d1a5;
  border-radius: 24px;
}
.product-card3 {
  background-color: #c2e297;
  border-radius: 24px;
}

.product-card1::before {
  content: '';
  position: absolute;
  top: -113px;
  left: 16px;
  width: 249px;
  height: 252px;
  background-repeat: no-repeat;
  background-size: contain;
  @include tab {
    top: -102px;
    left: 0;
    width: 220px;
    height: 226px;
    background-size: contain;
  }
  @include desk {
    top: -210px;
    width: 335px;
    height: 335px;
    background-size: contain;
  }
  background-image: url('../images/tel/product1-tel-1x.png');
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url('../images/tel/product1-tel-2x.png');
  }
  @media screen and (min-width: 481px) {
    background-image: url('../images/tab/product1-tab-1x.png');
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url('../images/tab/product1-tab-2x.png');
    }
  }

  @media screen and (min-width: 769px) {
    background-image: url('../images/desk/product1-desk-1x.png');
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url('../images/desk/product1-desk-2x.png');
    }
  }
}

