.product__title {
  @include tab {
    padding: 152px 23px 0;
  }
  @include desk {
    padding: 163px 60px 0;
    font-size: 30px;
    line-height: 1.13;
  }

  font-family: $font-family-title;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  margin-bottom: 66px;
  padding: 161px 53px 0;

  color: $text-color;
}

.product__text {
  @include tab {
    padding: 0 11px 0;
    margin-bottom: 40px;
    line-height: 1.78;
  }
  @include desk {
    padding: 0 44px 0;
    margin-bottom: 62px;
    font-size: 16px;
    line-height: 1.75;
  }
  position: relative;
  padding: 0 40px 0;
  font-family: $font-family-text;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  letter-spacing: 0.04em;

  margin-bottom: 30px;

  color: $text-color;
}

.product__text::before {
  @include tab {
    left: 97px;
  }
  @include desk {
    left: 151px;
  }
  content: '';
  width: 26px;
  height: 6px;
  top: -30px;
  left: 127px;
  position: absolute;
  background-image: url('../images/tel/dots.png');
  background-repeat: no-repeat;
}

.product__btn {
  border: none;
  background-color: transparent;
}
